<template>
  <div>
    <nv-card class="mb-base">
      <div class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3" @click="parent.refreshReportTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="parent.reportTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ parent.reportTablePagingInfo }}</span>
          </div>
        </div>
      </div>
    </nv-card>

    <div class="nv-row" v-for="(category, catIndex) in reportCategories" :key="catIndex">
      <div class="nv-col w-full mb-base">
        <h3>{{ catIndex }}</h3>
      </div>
      <div class="nv-col w-full sm:w-1/2"  v-for="(report, index) in category" :key="index">
        <nv-card class="mb-base">
          <div class="mb-4">
            <h4><i :class="[report.iconClass, 'mr-1']" v-if="report.iconClass"></i> {{ report.reportName | placeholder('-') }}</h4>
          </div>

          <div>
            <div class="nv-row mb-4" v-if="report.browseNotes">
              <div class="nv-col w-full">
                <div class="text-grey-dark">{{ report.browseNotes }}</div>
              </div>
            </div>
          </div>
          <vs-divider></vs-divider>
          <div class="flex justify-end">
            <vs-button class="p-3 ml-4" @click="reportRun(report.reportId)" type="filled" iconPack="far" icon="fa-play" v-if="$acl.check('isReportRun')">{{ $t('RUN') }}</vs-button>
          </div>
        </nv-card>
      </div>
    </div>

    <vs-pagination :total="parent.reportTableTotalPages" v-model="parent.reportTableCurrentPage" :max="parent.maxPaginationItems"></vs-pagination>
  </div>
</template>

<script>
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'report-card-layout',
  mixins: [NumberStyleMixin],
  props: {
    parent: {
      type: Object,
      required: true
    }
  },
  methods: {
    reportRun (id) {
      this.$router.push({ name: 'RunReport', params: { id: id } })
    }
  },
  computed: {
    reportCategories () {
      let categories = {}
      for (let i = 0; i < this.parent.reportTableData.length; i++) {
        let key = this.parent.reportTableData[i].categoryName
        if (categories.hasOwnProperty(key)) {
          categories[key].push(this.parent.reportTableData[i])
        } else {
          categories[key] = [this.parent.reportTableData[i]]
        }
      }
      return categories
    }
  }
}
</script>
