<template>
  <nv-card>
    <vs-table class="mb-base" :data="parent.reportTableData" :noDataText="parent.reportTableNoDataText" :sst="true" search @search="parent.filterReportTable" @sort="parent.orderReportTable">
      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
        <div class="flex flex-wrap-reverse items-center">
          <vs-button class="p-3" @click="parent.refreshReportTable" type="filled" icon="fa-sync" icon-pack="far" :disabled="parent.reportTableBusy">{{ $t('REFRESH') }}</vs-button>
        </div>

        <div class="flex justify-end mr-4">
          <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
            <span class="mx-2">{{ parent.reportTablePagingInfo }}</span>
            <vs-dropdown vs-trigger-click class="cursor-pointer mr-2">
              <span class="far fa-angle-down text-primary"></span>
              <vs-dropdown-menu>
                <vs-dropdown-item :key="index" v-for="(item, index) in parent.tablePageOptions" @click="parent.reportTablePerPage = item">
                  <span>{{ item }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </div>
      </div>

      <template slot="thead">
        <vs-th sort-key="reportName">
          {{ this.$t('REPORT_NAME') }}
        </vs-th>
        <vs-th sort-key="categoryName">
          {{ this.$t('CATEGORY') }}
        </vs-th>
      </template>

      <template slot-scope="{data}">
        <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
          <vs-td :data="data[index].reportName">
            <template v-if="$acl.check('isReportRun')">
              <router-link :to="{name: 'RunReport', params: {id: data[index].reportId}}">{{ data[index].reportName }}</router-link>
            </template>
            <template v-else>
              {{ data[index].reportName }}
            </template>
          </vs-td>
          <vs-td :data="data[index].categoryName">
            {{ data[index].categoryName | placeholder('-') }}
          </vs-td>
        </vs-tr>
      </template>
    </vs-table>

    <vs-pagination :total="parent.reportTableTotalPages" v-model="parent.reportTableCurrentPage" :max="parent.maxPaginationItems"></vs-pagination>
  </nv-card>
</template>

<script>
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'

export default {
  name: 'report-list-layout',
  mixins: [NumberStyleMixin],
  props: {
    parent: {
      type: Object,
      required: true
    }
  }
}
</script>
