<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`Report${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import ReportListLayout from '@/views/report/report-catalog/report-browse/ReportListLayout'
import ReportCardLayout from '@/views/report/report-catalog/report-browse/ReportCardLayout'
import ReportTableMixin from '@/mixins/report/ReportTableMixin'

export default {
  name: 'report-browse',
  mixins: [ReportTableMixin],
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  components: {
    ReportListLayout,
    ReportCardLayout
  },
  mounted: function () {
    this.loadReportTableData('loading-container', true)
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
